<script>
  import { screen } from './../../stores';
</script>

<style>
  .navigation {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 15px 0 0;
  }

  .game-nav {
    width: 95%;
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    list-style-type: none;
    width: 100%;
    column-gap: 10px;
  }
  ul li {
    display: block;
    width: 31.8%;
  }
  ul li button {
    width: 100%;
    margin: 0 0 10px;
  }
  ul li button.active {
    background-color: #d7d7d7;
  }
</style>

<div class="navigation">
  <ul class="main-nav">
    <li>
      <button 
        class="{$screen.currentView === 'intro' ? 'active' : ''}" 
        on:click={() => {$screen.currentView = 'intro'}}
      >
        Intro
      </button>
    </li>
    <li>
      <button 
        class="{$screen.currentView === 'setup' ? 'active' : ''}" 
        on:click={() => {$screen.currentView = 'setup'}}
      >
        Setup
      </button>
    </li>
    <li>
      <button 
        class="{$screen.currentView === 'game' ? 'active' : ''}" 
        on:click={() => {$screen.currentView = 'game'}}
      >
        Game
      </button>
    </li>
  </ul>

  {#if $screen.currentView === 'game'}
  <ul class="game-nav">
    <li>
      <button 
        class="{$screen.gameView === 'Screen1' ? 'active' : ''}" 
        on:click={() => {$screen.gameView = 'Screen1'}}
      >
        Screen 1
      </button>
    </li>
    <li>
      <button 
        class="{$screen.gameView === 'Screen2' ? 'active' : ''}" 
        on:click={() => {$screen.gameView = 'Screen2'}}
      >
        Screen 2
      </button>
    </li>
    <li>
      <button 
        class="{$screen.gameView === 'Screen3' ? 'active' : ''}" 
        on:click={() => {$screen.gameView = 'Screen3'}}
      >
        Screen 3
      </button>
    </li>
    <li>
      <button 
        class="{$screen.gameView === 'Screen4' ? 'active' : ''}" 
        on:click={() => {$screen.gameView = 'Screen4'}}
      >
        Screen 4
      </button>
    </li>
    <li>
      <button 
        class="{$screen.gameView === 'Screen5' ? 'active' : ''}" 
        on:click={() => {$screen.gameView = 'Screen5'}}
      >
        Screen 5
      </button>
    </li>
    <li>
      <button 
        class="{$screen.gameView === 'GameResult' ? 'active' : ''}" 
        on:click={() => {$screen.gameView = 'GameResult'}}
      >
        Game Result
      </button>
    </li>
  </ul>
  {/if}
</div>