import { game } from './stores';
import { screen } from './stores';
import { debug } from './stores';

export const screenFunctions = {
  handleOnNavigateAway: function () {
    let do_not_run = false;
    debug.subscribe((debug) => {
      do_not_run = debug.disable_exit_warning;
      return false;
    });

    if(do_not_run) return;

    const handleBeforeUnload = (event) => {
      game.subscribe((game) => {
        if(game.players.length > 0){
          event.preventDefault();
          event.returnValue = '';
        }
      })
    };

    window.addEventListener('beforeunload', handleBeforeUnload); //REVIEW: make this based on a debug variable

    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload); //REVIEW: make this based on a debug variable
    };
  },
};

export const gameFunctions = {
  playerColors: [
    "#8B0000", // Dark Red
    "#4682B4", // Steel Blue
    "#134d13", // Dark Green
    "#39204b", // Indigo
    "#7d2707", // Orange Red
    "#075a5a", // Dark Cyan
    "#85031d", // Crimson
    "#80711d", // Gold
    "#3b4b0c", // Olive
    "#696969", // Dim Gray
  ],
  playerCount: 0,
  minPlayers: 2,
  maxPlayers: 10,

  addPlayer: function (player) {
    if(
      !player.name ||
      player.name === ""
    ) {
      alert("Please fill out your name.");
      return;
    }

    if(
      this.playerCount >= this.maxPlayers
    ) {
      alert("Max players reached.");
      return;
    }

    game.update((game) => {
      player.color = this.playerColors[this.playerCount];
      game.players.push(player);
      return game;
    });

    this.playerCount += 1;
    this.playerCount = Math.min(this.maxPlayers, this.playerCount);
  },

  removePlayer: function (index) {
    game.update((game) => {
      game.players.splice(index, 1);
      return game;
    });
    this.playerCount -= 1;
    this.playerCount = Math.max(0, this.playerCount);
  },

  selectActivePlayer(index) {
    if(isNaN(index)) return;
    game.update((game) => {
      if(index >= game.players.length){
        index = 0;
      } else if(index < 0){
        index = game.players.length - 1;
      }
      game.players.forEach((player, i) => {
        player.active = false;
        if(i == index){
          player.active = true;
        }
      });
      return game;
    });
  },

  selectNextActivePlayer() {
    let active_index = 0;
    let player_found = false;
    game.update((game) => {
      game.players.forEach((player, i) => {
        if(player.active){
          player_found = true;
          player.active = false;
          active_index = i;
        }
      });
      return game;
    });

    if(!player_found) {
      this.selectActivePlayer(active_index);
    } else {
      this.selectActivePlayer(active_index + 1);
    }
  },

  selectPreviousActivePlayer() {
    let active_index = 0;
    let player_found = false;
    game.update((game) => {
      game.players.forEach((player, i) => {
        if(player.active){
          player_found = true;
          player.active = false;
          active_index = i;
        }
      });
      return game;
    });

    if(!player_found) {
      this.selectActivePlayer(active_index);
    } else {
      this.selectActivePlayer(active_index - 1);
    }
  },

  deselectActivePlayer() {
    game.update((game) => {
      game.players.forEach((player) => {
        player.active = false;
      });
      return game;
    });
  },

  getActivePlayer(){
    let player = null;
    game.subscribe((game) => {
      game.players.forEach((p) => {
        if(p.active){
          player = p;
          return;
        }
      });
    });
    return player;
  },

  getPlayerIndexByName(name){
    let index = null;
    game.subscribe((game) => {
      game.players.forEach((player, i) => {
        if(player.name == name){
          index = i;
          return game;
        }
      });
    });

    return index;
  },

  debug: function(type, event=null){
    switch(type){
      default:
        alert('The debug function: "' + type + '" is not yet implemented.');
      break;

      case 'reset game object':
        //If shift is not held down then prompt the user
        if(event && !event.shiftKey){
          let confirmation = confirm('Are you sure you want to reset the game object?');
          if(!confirmation) return;
        }
        game.update((game) => {
          game.round = 0;
          game.players = [];
          return game;
        });

        this.playerCount = 0;

        screen.update((screen) => {
          screen.currentView = 'setup';
          return screen;
        });
      break;

      case 'add player':
        //Check AddPlayer.svelte for the playerTemplate
        this.addPlayer({
          name: 'Player ' + (this.playerCount + 1), 
          additionalData: 'Additional data for player ' + (this.playerCount + 1),
          arrayData: ['Array data for player ' + (this.playerCount + 1)],
        });
      break;

      case 'remove player':
        this.removePlayer(this.playerCount-1);
      break;

      case 'select active player':
        this.selectActivePlayer();
      break;

      case 'select next active player':
        this.selectNextActivePlayer();
      break;

      case 'timer_speed':
        debug.update((debug) => {
          debug.timer_speed *= 2;
          if(debug.timer_speed > 64) debug.timer_speed = 2;
          return debug;
        });
      break;
    }
  }
}