<script>
  import "@andypf/json-viewer"; //github.com/andypf/json-viewer
  import { screen } from './../stores';
  import { game } from './../stores';
  import { debug } from './../stores';
  import { gameFunctions } from './../functions';
  import Navigation from './../Modules/core/Navigation.svelte';

  export let side = "left";

  function handleDebugClick(type, event) {
    gameFunctions.debug(type, event);
  }
</script>

<style>
  .debug-container.visible {
    display: block;
  }
  .debug-container {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    background: #f0f0f0;
    border: 1px solid #ccc;
    padding: 10px;
    text-align: left;
    width: 500px;
    max-height: 100vh;
    overflow-y: auto;
  }
  .debug-container.right {
    padding-top: 60px;
    right: 0;
    left: unset;
  }

  .action-buttons {
    border-top: 5px solid lightgray;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
    padding-top: 10px;
  }

  .action-buttons .game-buttons {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  .action-buttons .game-buttons button {
    width: 31.8%;
    margin: 0;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
  }
  .action-buttons .game-buttons button i {
    display: block;
  }

  .debug-properties {
    border-top: 5px solid lightgray;
    padding-top: 10px;
  }
  .debug-properties .property {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    background: #c8c8c8;
  }

  .debug-properties p {
    text-align: center;
  }

  .debug-properties .property.disable_exit_warning{
    opacity: .5;
  }
  .debug-properties .property.disable_exit_warning:after{
    content: "Only works on app load";
    text-align: center;
  }

  .debug-properties .property.visible{
    opacity: .5;
  }
  .debug-properties .property.visible:after{
    content: "Debug panels open/closed";
    text-align: center;
  }

  .debug-button {
    position: absolute;
    top: 10px;
    right: 20px;
    z-index: 10;
  }

  button {
    margin-bottom: 0;
  }

  @media screen and (max-width: 1520px) {
    .debug-button,
    .debug-container {
      display: none !important;
    }
  }
</style>

{#if side == 'right'}
<div class="debug-button">
  <button on:click={() => debug.update(data => {data.visible = !data.visible; return data;})}>
    <i class="fas fa-bug"></i>
  </button>
</div>
  {/if}

<div class="debug-container {side} {$debug.visible ? 'visible' : ''}">
  {#if $debug.visible && side === 'left'}
  <h3>Screen Object</h3>
  <andypf-json-viewer
    theme="monokai"
    indent="2"
    expanded="true"
    show-toolbar="true"
    show-data-types="false"
    expand-icon-type="square"
    data='{$screen}'
  ></andypf-json-viewer>
  <h3>Debug Object</h3>
  <andypf-json-viewer
    theme="monokai"
    indent="2"
    expanded="true"
    show-toolbar="true"
    show-data-types="false"
    expand-icon-type="square"
    data='{$debug}'
  ></andypf-json-viewer>
  <h3>Game Object</h3>
  <andypf-json-viewer
    theme="monokai"
    indent="2"
    expanded="true"
    show-toolbar="true"
    show-data-types="false"
    expand-icon-type="square"
    data='{$game}'
  ></andypf-json-viewer>
  {/if}

  {#if $debug.visible && side === 'right'}
  <h3>Navigation</h3>
  <Navigation />

  <div class="action-buttons">
    <h3>Game Object Buttons</h3>
    <div class="game-buttons">
      <button on:click={(event) => handleDebugClick('reset game object', event)} class="btn error">
        <i class="fas fa-refresh" />
        Reset game object
      </button>
      {#if $screen.currentView === 'setup'}
      <button on:click={() => handleDebugClick('add player')} class="btn success">
        <i class="fas fa-plus" />
        Add player
      </button>
      <button on:click={() => handleDebugClick('remove player')} class="btn warning">
        <i class="fas fa-times" />
        Remove player
      </button>
      {:else if $screen.currentView === 'game'}
      <button on:click={() => handleDebugClick('select next active player')} class="btn info">
        <i class="fas fa-chevron-right" />
        Next active player
      </button>
      {/if}
    </div>
    <div class="debug-properties">
      <h3>Debug Object Buttons</h3>
      {#each Object.keys($debug) as d}
        <div class="property {d}">
          {#if typeof $debug[d] === 'boolean'}
          <button class="btn {$debug[d] === true ? 'success' : 'error'}" on:click={() => $debug[d] = !$debug[d]}>
            <i class="fas fa-{$debug[d] === true ? 'check' : 'times'}"></i>
            {d}
          </button>
          {:else}
          <button class="btn info" on:click={() => handleDebugClick(d)}>{d}</button>
          <p>{$debug[d]}</p>
          {/if}
        </div>
      {/each}
    </div>
  </div>
  {/if}
</div>


