<!-- src/App.svelte -->
<script>
  import { screen } from './stores';
  import { onMount } from 'svelte';
  import Debug from './Screens/Debug.svelte';
  import Intro from './Screens/Intro.svelte';
  import Setup from './Screens/Setup.svelte';
  import Game from './Screens/Game.svelte';
  import { screenFunctions } from './functions';

  // Reactive assignment to switch views based on state
  $: currentView = $screen.currentView;

  onMount(() => {
    screenFunctions.handleOnNavigateAway();
  });
</script>

<style>
  .app-container {
    position: relative;
    max-width: 1520px;
    margin: 0 auto;
  }
  .container {
    background: #fff;
    width: 500px;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    max-width: 1100px;
    margin: 0 auto;
    z-index: 2;
    text-align: center;
    padding: 20px;
    min-height: 100vh;
  }
  @media screen and (max-width: 520px) {
    .container {
      width: 90%;
    }
  }
</style>

<div class="app-container">
  <Debug side="left" />
  <Debug side="right" />

  <div class="container">
    <div class="game">
      {#if currentView === 'intro'}
      <Intro />
      {:else if currentView === 'setup'}
        <Setup />
      {:else if currentView === 'game'}
        <Game />
      {/if}
    </div>
  </div>
</div>