<script>
  import { screen } from './../../stores';
  import { createEventDispatcher } from "svelte";

  export let setScreen = "";
  export let buttonText = "";
  export let icon = "fa-star";
  const dispatch = createEventDispatcher();

  function handleExtendClick(event) {
    dispatch('extend', event);
  }
</script>

<div>
  <button on:click={(event) => {$screen.currentView = setScreen; handleExtendClick(event);}}>
    <i class="fas {icon}"></i>
    {buttonText}
  </button>
</div>