<script>
  import { gameFunctions } from "../../functions";
  import { debug } from "../../stores";

  let playerTemplate = {
    name: {
      value: '',
      required: true,
      error: false,
    },
    additionalData: {
      value: '',
      required: false,
      error: false,
    },
    arrayData: {
      value: '',
      required: true,
      array: true,
      error: false,
    },
  };

  function addPlayer() {
    let error = false;
    let player = {};
    Object.keys(playerTemplate).forEach(key => {
      if(playerTemplate[key].required && !playerTemplate[key].value) {
        playerTemplate[key].error = (!playerTemplate[key].value);
        error = true;
      } else {
        if(typeof playerTemplate[key].array === 'undefined' && !playerTemplate[key].array) {
          player[key] = playerTemplate[key].value.trim();
        } else {
          player[key] = playerTemplate[key].value.split(',').map(item => item.trim());
        }
        playerTemplate[key].error = false;
        playerTemplate[key].value = "";
      }
    });
    if(error) return false;

    if($debug.console_logging){
      console.log(player);
    }

    gameFunctions.addPlayer(player);
  }
</script>

<style>
  .form-container {
    margin: 10px 0;
    text-align: center;
  }
  input,
  textarea {
    display: block;
    margin: 10px 0;
    padding: 10px;
    font-size: 1rem;
    width: 100%;
  }
  textarea {
    min-height: 150px;
  }
  .error {
    border: 2px solid var(--error);
  }
</style>

<div class="form-container">
  <h2>Add a player</h2>
  <input type="text" class={playerTemplate.name.error ? "error" : ""} bind:value={playerTemplate.name.value} placeholder="Enter your name..." />
  <textarea class={playerTemplate.additionalData.error ? "error" : ""} bind:value={playerTemplate.additionalData.value} placeholder="Enter additional data..."></textarea>
  <textarea class={playerTemplate.arrayData.error ? "error" : ""} bind:value={playerTemplate.arrayData.value} placeholder="Enter array data..."></textarea>
  <button on:click={addPlayer}>
    <i class="fa-solid fa-plus" />
    Add Player
  </button>
</div>

