<script>
  import { screen, game } from "../stores";
  import { gameFunctions } from "../functions";
  import { onMount } from "svelte";
  import Screen1 from "./GameScreens/Screen1.svelte";
  import Screen2 from "./GameScreens/Screen2.svelte";
  import Screen3 from "./GameScreens/Screen3.svelte";
  import Screen4 from "./GameScreens/Screen4.svelte";
  import Screen5 from "./GameScreens/Screen5.svelte";
  import GameResult from "./GameScreens/GameResult.svelte";

  $: gameView = $screen.gameView;

  onMount(() => {
    $screen.gameView = "Screen1";
    console.log($game);
    if($game.players.length < gameFunctions.minPlayers) {
      alert("Please add at least " + gameFunctions.minPlayers + " players.");
      $screen.currentView = "setup";
    }
  })
</script>

<style></style>

<div>
  {#if gameView === "Screen1" || gameView == "game"}
  <Screen1 />
  {:else if gameView === "Screen2"}
  <Screen2 />
  {:else if gameView === "Screen3"}
  <Screen3 />
  {:else if gameView === "Screen4"}
  <Screen4 />
  {:else if gameView === "Screen5"}
  <Screen5 /> 
  {:else if gameView === "GameResult"}
  <GameResult />
  {/if}
</div>