<script>
  export let players = [];
</script>

<style>
  div {
    background: black;
    color: white;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
  }
  div h2 {
    margin-bottom: 10px;
  }
  div p {
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
  }
</style>

<div>
  <h2>Player List</h2>
  {#if players.length > 0}
    {#each players as player}
      <p style="background-color: {player.color}">
        <i class="fas fa-user" />
        {player.name||""}
      </p>
    {/each}
  {:else}
  <p>No players</p>
  {/if}
</div>